import React from 'react';
import { Link } from 'react-router-dom';
import { NotLaptop } from '../common/layouts/Responsive';

const FavoriteKeyword = () => {
  return (
    <div className="favorite-keyword">
      <NotLaptop>
        <span> </span>
      </NotLaptop>
      <ul>
        {[
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
        ].map((item) => (
          <li key={item}>
            <Link>{item}</Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default FavoriteKeyword;
