import styled from 'styled-components';
import FormSearch from '../forms/FormSearch';
import { BtnSymbol } from '../buttons/Btn';
import { rem } from '../../../lib/styles/variables';
import palette from '../../../lib/styles/palette';
import { IconCrosshair } from '../Icons';
import { useCookies } from 'react-cookie';

const ModalChangeAddressBlock = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 15px;
  .__btnSymbol {
    font-size: ${rem(14)};
    .icon-crosshair {
      margin-right: 5px;
    }
  }
  .__failureMsg {
    padding: 20px 0 30px;
    p:first-child {
      font-weight: 700;
      font-size: ${rem(20)};
      margin-bottom: 15px;
    }
    p:last-child {
      font-size: ${rem(14)};
      line-height: 1.4;
    }
  }
  .__addressList {
    flex: 1 1 auto;
    height: 0px;
    overflow: auto;
  }
`;

const ModalChangeAddress = ({ setShowModal }) => {
  return (
    <ModalChangeAddressBlock>
      <div className="__form">
        <FormSearch placeholder="  " autoFocus />
      </div>
      <div className="__btnSymbol">
        <BtnSymbol fullWidth fw="700">
          <IconCrosshair />
             
        </BtnSymbol>
      </div>
      <div className="__failureMsg">
        <FailureMsg />
      </div>
      <div className="__addressList">
        <AddressList setShowModal={setShowModal} />
      </div>
    </ModalChangeAddressBlock>
  );
};

/*    */
const FailureMsg = () => {
  return (
    <>
      <p>""    .</p>
      <p>
            ,  
        .
      </p>
    </>
  );
};

/*   */
const AddressListBlock = styled.div`
  span {
    color: #006199;
    font-size: ${rem(13)};
    font-weight: 500;
  }
  button {
    padding: 15px 0;
    border-bottom: 1px solid ${palette.gray[3]};
    width: 100%;
  }
`;

const AddressList = ({ setShowModal }) => {
  const [, setCookie] = useCookies(['address']);
  return (
    <AddressListBlock>
      <span></span>
      <ul>
        {[
          ' ',
          ' ',
          ' ',
          ' ',
          ' ',
          ' ',
          ' ',
        ].map((item) => (
          <li key={item}>
            <button
              onClick={() => {
                // setAddress(item);
                setCookie('address', item);
                setShowModal(false);
              }}
            >
              {item.replace(' ', ', ')}
            </button>
          </li>
        ))}
      </ul>
    </AddressListBlock>
  );
};

export default ModalChangeAddress;
