import { Link } from 'react-router-dom';
import { IconArrowUpRight, IconDropdown } from '../common/Icons';

const Gnb = ({ albaRef }) => {
  return (
    <nav>
      <ul>
        <li>
          <Link to="/buy-sell"></Link>
        </li>
        <li>
          <Link to="/"></Link>
        </li>
        <li>
          <Link to="/"></Link>
        </li>
        <li className="alba" ref={albaRef}>
          <Link to="/" className='alba-link' onClick={() => albaRef.current.classList.toggle('open')}>
            
            <button className="btn-dropdown">
              <IconDropdown $fz="0.75em" />
            </button>
          </Link>
          <ul>
            <li>
              <Link> </Link>
            </li>
            <li>
              <Link> </Link>
            </li>
            <li>
              <Link>
                  <IconArrowUpRight />
              </Link>
            </li>
            <li>
              <Link>
                  <IconArrowUpRight />
              </Link>
            </li>
          </ul>
        </li>
        <li>
          <Link to="/"></Link>
        </li>
        <li>
          <Link to="/"></Link>
        </li>
        <li>
          <Link to="/"></Link>
        </li>
      </ul>
    </nav>
  );
};

export default Gnb;
