import FilterLayout from '../common/layouts/market-layout/FilterLayout';
import FilterList from '../common/layouts/market-layout/FilterList';
import LocationFilter from '../common/layouts/market-layout/special-filter/LocationFilter';

const filterData = [
  {
    heading: '',
    type: 'radio',
    queryKey: 'category',
    list: [
      { name: '', queryValue: '1' },
      { name: '', queryValue: '2' },
      { name: '/', queryValue: '3' },
      { name: '/', queryValue: '4' },
      { name: '', queryValue: '5' },
      { name: '', queryValue: '6' },
      { name: '', queryValue: '7' },
      { name: '', queryValue: '8' },
      { name: '/', queryValue: '9' },
      { name: '/', queryValue: '10' },
      { name: '/', queryValue: '11' },
      { name: '//', queryValue: '12' },
      { name: '', queryValue: '13' },
      { name: '/', queryValue: '14' },
      { name: '', queryValue: '15' },
      { name: '', queryValue: '16' },
      { name: '', queryValue: '17' },
      { name: '', queryValue: '18' },
      { name: ' ', queryValue: '19' },
      { name: '', queryValue: '20' },
    ],
  },
  {
    heading: '',
    type: 'radio',
    queryKey: 'sort_by',
    list: [
      { name: '', queryValue: 'recent' },
      { name: '', queryValue: 'hottest' },
    ],
  },
  {
    heading: '',
    type: 'buttonWithInput',
    queryKey: 'price',
    list: [
      { name: '', queryValue: '0__0' },
      { name: '5,000 ', queryValue: '0__5000' },
      { name: '10,000 ', queryValue: '0__10000' },
      { name: '20,000 ', queryValue: '0__20000' },
    ],
  },
];

const BuySellFilter = () => {
  return (
    <FilterLayout>
      <BuySellFilterContent />
    </FilterLayout>
  );
};

const BuySellFilterContent = () => {
  return (
    <>
      <LocationFilter />
      {filterData.map((data) => (
        <FilterList
          key={data.heading}
          heading={data.heading}
          list={data.list}
          type={data.type}
          queryKey={data.queryKey}
        />
      ))}
    </>
  );
};

export default BuySellFilter;
